#home .banner-area{
    background-position: center center;
    background-size:fill ;
    height: 100vh;
    background-color: #D4F1F4;
    width: auto;
    margin: auto;
}

#home .banner-text{
    position: absolute;
    width: 600px;
    height: 300px;
    margin: 20% 30%;
    text-align: center;
}
#home .banner-text h1{
    color:hsla(212, 50%, 47%, 0.454);
    text-transform: uppercase;
    font-size: 60px;
    font-family: 'poppins' , sans-serif;
    animation: scale-up-top 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}
@keyframes scale-up-top {
    0% {
      transform: scale(0.2);
      transform-origin: 50% 0%;
    }
    100% {
      transform: scale(1);
      transform-origin: 50% 0%;
    }
  }
#home .banner-text p{
    color: #000000;
    font-size: 18px;
}
#home .banner-text a{
    padding: 10px 25px;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 14px;
    margin-top: 20px;
    display: inline-block;
    color: #9372cf;
}
#home .banner-text a:hover{
    background-color: white;
    color: rgba(68, 25, 109, 0.623);
    font-weight: bold;
}
