#empdetails {
  display: flex;
  align-items: center;
  padding-left: 100px;
}
#empdetails table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  margin-top: 20px;
  /* background-color: white; */
  display: inline-block;
  padding-left: 100px;
}

table td,
table th {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: left;

}
table th {
    background-color: #88c2d2;
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    padding: 12px;
    text-align: left;
} 
 