#pagination  td,
 th {
  border: 1px solid #ddd;
  padding: 12px;
  font-family: sans-serif;
  border: 1px solid rgb(183, 174, 174);
  padding: 10px 20px;
  font-size: medium;
}

#pagination th {
  background-color: rgb(235, 115, 115);
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  padding: 12px;
  text-align: center;
}

#pagination tr:hover {
  background-color: #ddd;
}

.controls button {
  padding: 10px;
  margin: 5px;
  border-radius: 10%;
  background-color: yellow;
  transition: all 0.2s;
}
.controls button.currentpage {
  background-color: rgb(12, 35, 245);
  color: white;
}
.controls button:active {
  transform: scale(0.80);

}