nav {
    /* position: absolute; */
    top:0;
    z-index: 100;
    left: 0;
    right: 0;
    border: solid 2px black;
    background-color: #92d6e4;
    backdrop-filter: blur(3px) ;
    text-align: right;
}
nav a {
    text-decoration: none;
    color: black;
    padding: 10px;
    display: inline-block;
}
nav li {
    display: inline-block;
}
nav ul {
    margin-top: 0px;
}
nav a:hover{
    color: bisque;
    font-size: large;
}
