.card {
    border: 2px solid black;
    background-color: rgb(198, 215, 243);
    color: rgb(0, 0, 0);
    margin: 5px;
    padding: 12px;
    display: inline-block;
    text-align: center;
    border-radius: 10%;
    height: 300px;
    width: 200px;
}

.card img{
    width: 160px;
    border-radius: 10%;
}
.card p{
    padding: 5px;
    font-family:Arial, Helvetica, sans-serif;
}
.card button{
    display: inline-block;
    padding: 10px 10px;
    color: #57c2c7;
    border-radius: 20%;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    cursor: pointer;
}