#signupform form{
  position: absolute;
  top: 16%;
  left:35%;
  width: 400px;
  padding: 35px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 10%;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.6);
}

#signupform h2 {
  margin: 0 0 25px;
  padding: 0;
  color: #fff;
  text-align: center;
  cursor: pointer;
}

/* #signupform .form-group {
  position: relative;
} */

#signupform input {
  width: 100%;
  padding: 13px 0;
  font-size: 15px;
  color: #ffffff;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  background: transparent;
}


#signupform  button {
  /* position: relative; */
  /* display: inline-block; */
  padding: 10px 20px;
  color: #000000;
  font-size: 16px;
  /* text-decoration: none; */
  text-transform: uppercase;
  /* overflow: hidden; */
  transition: 0.5s;
  margin-top: 25px;
  margin-bottom: 5px;
  letter-spacing: 1px;
  border-radius: 30px;
  cursor:pointer;
}

#signupform  button:hover {
  background: #03e9f4;
  color: #f3f3f3;
  border-radius: 5px;
  box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4, 0 0 100px #03e9f4;
}

#signupform form {
  text-align: center;
  margin-top: 20px;
}

#signupform  button {
  color: #000000;
  text-decoration: none;
}


#signupform p {
  color: rgb(181, 140, 140);
  word-spacing: 2px;
} 

#signupform i,a{
  color: white;
  padding-right: 10px;
  text-decoration: none;
}
#signupform ::placeholder{
  color: white;
  
}
